<template>
  <v-container fluid :class="$vuetify.breakpoint.mdAndUp ? 'pa-0' : ''">
    <tool-bar></tool-bar>
    <v-row>
      <v-col cols="12" lg="6">
        <app-layout title="New Category">
          <v-col v-for="n in 6" :key="n" cols="12" md="6">
            <category-card
              isHoverAndRipple
              title="Landscape design"
              subTitle="Greyhound divisely hello coldly fonwderfully"
              :id="n.toString()"
            >
            </category-card>
          </v-col>
        </app-layout>
      </v-col>
      <v-col cols="12" lg="6">
        <app-layout title="All Category">
          <v-col v-for="n in 24" :key="n" cols="12" md="6">
            <app-card-horizon isHoverAndRipple>
              <template v-slot:img>
                <v-img
                  src="https://picsum.photos/500/300?random"
                  lazy-src="https://picsum.photos/10/6?random"
                  height="100%"
                  width="100%"
                >
                </v-img>
              </template>
              <v-list-item two-line class="text-right">
                <v-list-item-content>
                  <v-list-item-title
                    class="headline mb-1"
                    style="white-space: normal;"
                    >Landscape design</v-list-item-title
                  >
                  <v-list-item-subtitle style="white-space: normal;"
                    >Greyhound divisely hello coldly
                    fonwderfully</v-list-item-subtitle
                  >
                </v-list-item-content>
              </v-list-item>
            </app-card-horizon>
          </v-col>
          <v-col cols="12" md="12">
            <v-row>
              <v-col cols="12" md="12">
                <v-pagination
                  v-model="page"
                  :length="30"
                  :total-visible="$vuetify.breakpoint.mdAndUp ? 7 : 5"
                  color="secondary"
                ></v-pagination>
              </v-col>
            </v-row>
          </v-col>
        </app-layout>
      </v-col>
    </v-row>
    <v-alert dense type="error" class="alert-fix" :value="isError">
      <strong> Connection failed.</strong> Try next time
      <v-btn icon color="white" right @click.stop="closeAlert()">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-alert>
  </v-container>
</template>

<script>
import axios from 'axios'

import AppLayout from '../components/AppLayout.vue'
import toolBar from '../components/TheToolBarHome.vue'

export default {
  name: 'categories-page',
  components: {
    'app-layout': AppLayout,
    'app-card-horizon': () => import('../components/AppCardHorizon.vue'),
    'category-card': () => import('../components/CategoryCard.vue'),
    'tool-bar': toolBar
  },
  data: () => ({
    loading: true,
    error: '',
    page: 1,
    data: []
  }),
  computed: {
    isError: function () {
      return this.error !== ''
    }
  },
  created: function () {
    // console.log(this.avatar)
    this.fetchData()
  },
  methods: {
    fetchData: async function () {
      /* axios
        .get('/categories', {
          params: {
            limit: 12,
            page: 1
          }
        })
        .then(function (response) {
          this.data = response.data.data
          console.log(this.data)
          this.loading = false
        })
        .catch(function (err) {
          // console.error(err)
          this.error = err
          this.loading = true
        })
        .then(function () {
          // always executed
        }) */

      try {
        const response = await axios.get('/categories', {
          params: {
            limit: 12,
            page: 1
          }
        })
        console.log(response)
        this.data = response.data.data

        this.loading = false
      } catch (err) {
        console.error(err)
        this.error = err

        this.loading = true
      }
      //  `getPost`     /   API
      /* getPost(this.$route.params.id, (err, post) => {
        this.loading = false
        if (err) {
          this.error = err.toString()
        } else {
          this.post = post
        }
      }) */
    },
    toMovement: function () {
      this.$router.push({
        path: '/view',
        params: { id: this.movementId }
      })
    },
    closeAlert: function () {
      this.error = ''
    }
  }
}
</script>

<style lang="scss" scoped>
.alert-fix {
  position: fixed;
  bottom: 0;
  left: 50%;
  width: 90%;
  z-index: 10;
  transform: translateX(-50%);
}
</style>
